import React, { Component } from 'react'
import Select from 'react-select'
import InputField from '../../Elements/InputField';
import CheckBoxField from '../../Elements/CheckboxField';

export default class SlackTab extends Component {
    componentDidMount() {
        const { accounts } = this.props;

        if (accounts && accounts.length > 0) {
            this.props.onSelect(accounts[0].ConnectedAccounts.id);
        }
    }
    render() {
        const { accounts, tab, moderation, onCheckHandler, onSelect, error, keyword, handleInput, selectAccount, channelError } = this.props;

        const options = accounts ? accounts.map((account) => ({ label: account.ConnectedAccounts.value2, value: account.ConnectedAccounts.id })) : [];
        return (
            <>
                <div className="row margin-bottom-20 mb-5">
                    <div className="col-md-6 titleLabel">
                        <label className="text-dark-75 font-weight-bolder">Select Channel</label>
                        <Select className="selectpicker" menuPlacement="auto" classNamePrefix="selectpicker" onChange={(e) => onSelect(e.value)} options={options} value={options[options.findIndex(i => i.value == selectAccount)]} />
                        {channelError && <span className="errorMessage required">This field is required</span>}
                    </div>
                    {tab == "ChannelKeyword" ?
                        <div className="col-md-6 titleLabel keywordSection">
                            <label className="text-dark-75 font-weight-bolder">Keyword</label>
                            <InputField name={"keyword"} className="form-control form-control-lg fw-bolder px-9" placeholder="Enter keyword" value={keyword} onChangeHandler={handleInput} />
                            {error && <span className="errorMessage required">This field is required</span>}
                        </div>
                        : null}
                </div>
                {/* <div className="form-group hideforBSearch">
                    <div className="pb-2">
                        <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                            <CheckBoxField isChecked={moderation} onChangeHandler={onCheckHandler} name={"moderation"}>
                                <span className="form-check-label">Manually approve posts before making them public?</span>
                            </CheckBoxField>
                        </label>
                    </div>
                </div> */}
            </>
        )
    }
}
