import React, { Component } from 'react';
// import { AUTOCOMPLETE } from "../../../../actions/manageFeedsAction";
import AccountPicker from '../../Elements/AccountPicker';
import { debounce } from '../../../../../Services/Utils/utils';
import InputField from '../../Elements/InputField';
import CheckBoxField from '../../Elements/CheckboxField';
import InfoSection from '../../Elements/InfoSection';
import { autoComplete } from '../../../../../Services/Actions/addFeedAction';

class TwitterTabs extends Component {
    state = {
        showList: false,
        lists: [],
    }

    autoComplete = debounce((e) => {
        autoComplete({
            networkId: 1,
            handle: e.target.value,
        }).then(res => {
            const { data } = res
            if (data.response_code == 200) {
                this.setState({ lists: data.responseData.data, showList: true })
            }
            else this.setState({ lists: [], showList: false })
        })
    })

    handleAutoComplete = (e) => {
        this.props.onChangeHandler(e);
        this.autoComplete(e);
    }

    onSelectList = (item) => {

        const { onListNameChange, onListIdChange } = this.props
        onListNameChange(item.name);
        onListIdChange(item.id, item.owner_id);
        this.setState({ showList: false })
    }

    render() {
        const { lists, showList } = this.state
        const { hashtag, handle, networkId, onConnectedAccountChange, userName, showListError, listNameError, listName, moderation, advanced, tab, error, handleError, mentionError, advanceError, multiPhoto, listname, excludeRetweets, onChangeHandler, excludeReplies, onCheckHandler, mentions, favorites, favoriteError, connectedAccountList } = this.props;

        return (
            <>
                <div className="mb-2 mb-xl-3">
                    <div className="">
                        {tab != 'list' ? <InputField name={tab} className={`form-control form-control-lg fw-bolder px-9 ${(tab == "hashtag" && error)||(handleError && tab == "handle")||(mentionError && tab == "mentions")||(favoriteError && tab == "favorites")||(advanceError && tab == "advanced")?'is-invalid':''}`} placeholder={tab !== "advanced" ? `Enter ${tab !== 'favorites' ? tab : 'the twitter handle of the user to import Liked Posts from.'}` : 'Query'} value={tab == "hashtag" ? hashtag : tab == "handle" ? handle : tab == "mentions" ? mentions : tab == "advanced" ? advanced : favorites} onChangeHandler={onChangeHandler} /> : <div className="row">
                            <div className="col-md-6">
                                <div className="form-group">
                                    <InputField name={"userName"} placeholder="Enter Keyword" className={`form-control form-control-lg fw-bolder px-9 ${showListError?'is-invalid':''}`} value={userName} onChangeHandler={(e) => this.handleAutoComplete(e)} autoComplete={true} />
                                    {showList ? lists && lists.length > 0 ? lists.map((item, index) => (
                                        <li key={item.id} onClick={() => this.onSelectList(item)}>
                                            <div className="content-section">
                                                {item.name}
                                                <p>https://twitter.com/{item.name}</p>
                                            </div>
                                        </li>
                                    )) : null : null}
                                    {showListError && <span className="errorMessage required">This field is required.</span>}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group">
                                    <InputField name={"listName"} placeholder="Enter Username" className={`form-control form-control-lg fw-bolder px-9 ${ listNameError ?'is-invalid':''}`} value={listName} onChangeHandler={onChangeHandler} autoComplete={true} />
                                    {listNameError && <span className="errorMessage required">This field is required.</span>}
                                </div>
                            </div>
                        </div>}
                        {tab == "advanced" ? <span className="help-block">Use the table below to construct your query.</span> : null}
                        {(tab == "hashtag" && error)||(handleError && tab == "handle")||(mentionError && tab == "mentions")||(favoriteError && tab == "favorites")||(advanceError && tab == "advanced") ? <span className="errorMessage required">This field is required</span> : null}
                        {
                            tab == "advanced" ? <div className="w-100 my-5" style={{ height: "250px", overflowY: "auto" }}>
                                <div className="table-responsive">
                                    <table className="table table-hover table-light">
                                        <thead>
                                            <tr>
                                                <th style={{ width: "25%" }}><strong>Filter By:</strong></th>
                                                <th><strong>Example</strong></th>
                                                <th><strong>Targets Tweets:</strong></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td className="bold">
                                                    <i className="far fa-comment"></i>
                                                    Keywords
                                                </td>
                                                <td style={{ width: "166px" }}>grapes banana</td>
                                                <td>containing both "grapes" and "banana".</td>
                                            </tr>
                                            <tr>
                                                <td><strong></strong></td>
                                                <td>"good time"</td>
                                                <td>containing the exact phrase "good time"</td>
                                            </tr>
                                            <tr>
                                                <td><strong></strong></td>
                                                <td>die OR alive</td>
                                                <td>containing either "die" or "alive" (or both)</td>
                                            </tr>
                                            <tr>
                                                <td><strong></strong></td>
                                                <td>food -drink</td>
                                                <td>containing "food" but not "drink"</td>
                                            </tr>
                                            <tr>
                                                <td><strong></strong></td>
                                                <td>#soul</td>
                                                <td>containing the hashtag "#soul"</td>
                                            </tr>
                                            <tr>
                                                <td className="bold"></td>
                                                <td>service :) </td>
                                                <td>containing "service" and with a positive attitude.</td>
                                            </tr>
                                            <tr>
                                                <td className="bold"></td>
                                                <td>service :( </td>
                                                <td>containing "service" and with a negative attitude.</td>
                                            </tr>
                                            <tr>
                                                <td className="bold"><i className="fas fa-mobile-alt"></i> Questions</td>
                                                <td>weather ?</td>
                                                <td>containing "weather" and asking a question.</td>
                                            </tr>
                                            <tr>
                                                <td className="bold"><i className="fas fa-user"></i> User</td>
                                                <td>from:taggbox</td>
                                                <td>sent from the user @taggbox</td>
                                            </tr>
                                            <tr>
                                                <td><strong></strong></td>
                                                <td>to:taggbox</td>
                                                <td>sent to the user @taggbox</td>
                                            </tr>
                                            <tr>
                                                <td><strong></strong></td>
                                                <td>@taggbox</td>
                                                <td>mentioning @taggbox</td>
                                            </tr>
                                            <tr>
                                                <td className="bold">
                                                    <i className="fas fa-location-arrow"></i>
                                                    Location
                                                </td>
                                                <td>#dogs geocode:34.0522342,-118.2436849,10km</td>
                                                <td>
                                                    containing hashtag "#dogs" within Los Angeles Get coordinates (10km radius)
                                                    <a tabIndex="-1" target="_blank" href="https://www.gps-coordinates.org/">here</a>
                                                </td>
                                            </tr>
                                            <tr style={{ display: "none" }}>
                                                <td className="bold">
                                                    <i className="fas fa-mobile-alt"></i>
                                                    Devices / Apps
                                                </td>
                                                <td>news source:instagramfeed</td>
                                                <td>
                                                    containing "news" and entered via InstagramFeed
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>
                                                    <strong>
                                                        <i className="fas fa-link"></i>
                                                        Content
                                                    </strong>
                                                </td>
                                                <td>funny filter:links</td>
                                                <td>containing " funny" and with a URL.</td>
                                            </tr>
                                            <tr>
                                                <td className="bold"><i className="far fa-calendar-alt"></i> Dates</td>
                                                <td>champion since:2012-12-24</td>
                                                <td>containing " champion" and sent since date "2012-12-24" (year-month-day).</td>
                                            </tr>
                                            <tr>
                                                <td className="bold"></td>
                                                <td>champion until:2012-12-24</td>
                                                <td>containing " champion" and sent before the date "2012-12-24".</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div> : null
                        }
                    </div>
                </div>
                <div className="pb-2">
                    <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={multiPhoto} onChangeHandler={onCheckHandler} name={"multiPhoto"}>
                            <span className="form-check-label">Add Multiple Photos</span>
                        </CheckBoxField>
                    </label>
                    {tab != 'list' && tab != "advanced" ? <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={excludeRetweets} onChangeHandler={onCheckHandler} name={"excludeRetweets"}>
                            <span className="form-check-label">Exclude Retweets</span>
                        </CheckBoxField>
                    </label> : null}
                    {tab != 'favorites' && tab != 'list' ? <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={excludeReplies} onChangeHandler={onCheckHandler} name={"excludeReplies"}>
                            <span className="form-check-label">Exclude Replies</span>
                        </CheckBoxField>
                    </label> : null}
                    {/* <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={moderation} onChangeHandler={onCheckHandler} name={"moderation"}>
                            <span className="form-check-label">Manually approve posts before making them public?</span>
                        </CheckBoxField>
                    </label> */}
                </div>
                {(connectedAccountList && connectedAccountList.length > 0) && <AccountPicker connectedAcc={connectedAccountList} networkId={networkId} onConnectedAccountChange={onConnectedAccountChange} />}
                {tab != 'list' && tab != "advanced" && tab != "favorites" ?
                    <InfoSection text={tab == 'hashtag' ? `Only 10 tweets will be aggregated from the last 7 days' data, after which all the upcoming tweets will be aggregated.` : tab == 'handle' ? `Initially, 10 tweets will be aggregated from the Twitter handle, after which all the upcoming tweets will be aggregated.` : tab == 'mentions' ? `Only 10 mentions from the last 7 days will be aggregated, after which all the upcoming tweets will be aggregated.` : ``} />
                    : null
                }
            </>
        )
    }
}

export default TwitterTabs

