import React, { Component } from 'react'
import SearchPlace from '../../Elements/SearchPlace';
import CheckBoxField from '../../Elements/CheckboxField'
import InputField from '../../Elements/InputField';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Dropdown } from 'react-bootstrap';
import { youtubeAutoComplete } from '../../../../../Services/Actions/addFeedAction';
import { debounce } from '../../../../../Services/Utils/utils';


export default class YoutubeTabs extends Component {

    state = {
        list: [],
        spinnerActive: false
    }

    autoComplete = debounce((query, type) => youtubeAutoComplete(query, type).then(res => {
        const { data, status } = res
        if (status == 200) {
            this.setState({ list: data.items })
        }
    }))


    onYoutubeChange = query => {
        const { onValueChange, tab } = this.props
        this.setState({ spinnerActive: true })
        if (["Play List", "Channel Url", "Shorts"].includes(tab)) {
            try {
                const type = tab == "Play List" ? "playlist" : "channel"
                onValueChange(query);
                this.autoComplete(query, type);
                this.setState({ spinnerActive: false })
            } catch (error) {
                console.log(error)
                this.setState({ spinnerActive: false })
            }
        }
        else {
            onValueChange(query);
            this.setState({ spinnerActive: false })
        }
    }

    onListClick = (item) => {
        try {
            const { onValueChange, tab, onChannelIdChange, } = this.props
            onValueChange(tab == "Play List" ? item.snippet.title : item.snippet.channelTitle)
            onChannelIdChange(`${tab == "Play List" ? item.id.playlistId : tab == "Shorts" ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId}`)
            document.getElementById(`feedsfield-${tab}`).value = `https://www.youtube.com/${tab == "Play List" ? "playlist?list=" : "channel/"}${tab == "Play List" ? item.id.playlistId : tab == "Shorts" ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId}`
            this.setState({ list: [] })
        } catch (error) {
            console.log(error)
        }
    }


    feedIcon = (iconId) => {
        switch (iconId) {
            case 'Channel Url': // @
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M18.3643 15.5353L16.95 14.1211L18.3643 12.7069C20.3169 10.7543 20.3169 7.58847 18.3643 5.63585C16.4116 3.68323 13.2458 3.68323 11.2932 5.63585L9.87898 7.05007L8.46477 5.63585L9.87898 4.22164C12.6127 1.48797 17.0448 1.48797 19.7785 4.22164C22.5121 6.95531 22.5121 11.3875 19.7785 14.1211L18.3643 15.5353ZM15.5358 18.3638L14.1216 19.778C11.388 22.5117 6.9558 22.5117 4.22213 19.778C1.48846 17.0443 1.48846 12.6122 4.22213 9.87849L5.63634 8.46428L7.05055 9.87849L5.63634 11.2927C3.68372 13.2453 3.68372 16.4112 5.63634 18.3638C7.58896 20.3164 10.7548 20.3164 12.7074 18.3638L14.1216 16.9496L15.5358 18.3638ZM14.8287 7.75717L16.2429 9.17139L9.17187 16.2425L7.75766 14.8282L14.8287 7.75717Z" fill="currentColor"></path></svg>
                break;
            case 'Shorts': // Shorts
                return <svg xmlns="http://www.w3.org/2000/svg" width="15.259" height="19.948" viewBox="0 0 15.259 19.948">
                    <path id="Path_3" data-name="Path 3" d="M.549,17.631a4.369,4.369,0,0,1,1.49-5.75,3.972,3.972,0,0,1-1.932-2.7A4.248,4.248,0,0,1,2.164,4.365c1.65-.986,3.338-1.95,4.972-2.883q.815-.467,1.633-.935A4.192,4.192,0,0,1,10.838,0a4.48,4.48,0,0,1,2.849,1.037A4.274,4.274,0,0,1,15.21,3.763a4.374,4.374,0,0,1-2.037,4.355,4.369,4.369,0,0,1,.471,7.115l-.007.006a7.809,7.809,0,0,1-1.117.718c-.1.058-.205.115-.306.175-1.575.917-3.647,2.12-5.711,3.288l-.014.007a4.384,4.384,0,0,1-5.941-1.8ZM9.513,1.85q-.815.468-1.634.935C6.252,3.713,4.57,4.674,2.935,5.651A2.742,2.742,0,0,0,1.586,8.93a2.552,2.552,0,0,0,1.373,1.754c.282.158.571.325.964.558L5,11.883l-1.079.647c-.375.226-.717.428-1.088.621a2.884,2.884,0,0,0,1.588,5.3,2.817,2.817,0,0,0,1.345-.339c2.054-1.162,4.118-2.36,5.686-3.274.107-.063.217-.124.326-.185a6.555,6.555,0,0,0,.9-.573,2.872,2.872,0,0,0,1.06-2.485,2.928,2.928,0,0,0-1.528-2.3c-.163-.093-.316-.184-.478-.282-.133-.081-.272-.165-.419-.248l-1.09-.629,1.072-.66c.3-.184.606-.365.939-.553l.012-.007a2.87,2.87,0,0,0,1.471-2.938l0-.013a2.773,2.773,0,0,0-.995-1.774A2.977,2.977,0,0,0,10.838,1.5,2.641,2.641,0,0,0,9.513,1.85ZM5.586,6.735,11.248,10,5.586,13.28ZM6.8,11.171,8.819,10,6.8,8.839Z" fill="currentColor" />
                </svg>
                break;
            case 'Play List': // Play List
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M22 18V20H2V18H22ZM2 3.5L10 8.5L2 13.5V3.5ZM22 11V13H12V11H22ZM4 7.1085V9.8915L6.22641 8.5L4 7.1085ZM22 4V6H12V4H22Z" fill="currentColor"></path></svg>
                break;
            case 4: // Keyword
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M13 6V21H11V6H5V4H19V6H13Z" fill="currentColor"></path></svg>
                break;
            case 'Location': // Location
                return <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M12 23.7279L5.63604 17.364C2.12132 13.8492 2.12132 8.15076 5.63604 4.63604C9.15076 1.12132 14.8492 1.12132 18.364 4.63604C21.8787 8.15076 21.8787 13.8492 18.364 17.364L12 23.7279ZM16.9497 15.9497C19.6834 13.2161 19.6834 8.78392 16.9497 6.05025C14.2161 3.31658 9.78392 3.31658 7.05025 6.05025C4.31658 8.78392 4.31658 13.2161 7.05025 15.9497L12 20.8995L16.9497 15.9497ZM12 13C10.8954 13 10 12.1046 10 11C10 9.89543 10.8954 9 12 9C13.1046 9 14 9.89543 14 11C14 12.1046 13.1046 13 12 13Z" fill="currentColor"></path></svg>
                break;
            default:
                break;
        }
    }
    render() {
        const { tab, onValueChange, checkBox, onCheckBoxChange, onKeywordChange, keyword, error, onLatlanChange, shortError, playListError, keywordError, locationError, locationKeywordError } = this.props
        const { list, spinnerActive } = this.state

        return (
            <>
                <div className="row">
                    <div class={`col-md-${tab == "Location" ? '6' : '12'}`}>
                        <div className="mb-2 mb-xl-3">
                            {tab == "Location" ?
                                <SearchPlace onFieldTextChange={onValueChange} onLatlanChange={onLatlanChange} error={(tab == "Location" && locationError)}/>
                                :
                                <Dropdown className={`w-100 ${error ? 'is-invalid' : ''}`} autoClose="outside">
                                    <Dropdown.Toggle variant='' className="p-0 w-100 arrow_disabled position-relative">
                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex" style={{ zIndex: 9 }}>
                                            {this.feedIcon(tab)}
                                        </span>
                                        <input placeholder={`${tab == "Location" ? "" : "Enter"} ${tab == "Shorts" || tab == "Channel Url" ? "channel url" : tab == "Play List" ? "Play List ID" : tab == "Keywords" ? "keyword" : ""}`} className={`form-control form-control-lg fw-bolder px-9 ${(tab == "Channel Url" && error) ||(tab == "Shorts" && shortError)||(tab == "Play List" && playListError)||(tab == "Keywords" && keywordError)||(tab == "Location" && locationError)?'is-invalid':''}`} id={`feedsfield-${tab}`} type="text" onChange={e => this.onYoutubeChange(e.target.value)} />
                                        {spinnerActive ? <div class="spinner" style={{ top: -22 }}></div> : null}
                                    </Dropdown.Toggle>
                                    {list && list.length ?
                                        <Dropdown.Menu className="w-100">
                                            <PerfectScrollbar options={{ wheelPropagation: false }}>
                                                <div className="feedDropdown">
                                                    {list.map(item => (
                                                        <Dropdown.Item onClick={() => this.onListClick(item)}>
                                                            <div class="d-flex algin-items-center">
                                                                <img class="h-30px w-30px object-center-cover rounded-1 me-1" src={item.snippet.thumbnails.default.url} />
                                                                <div className="d-flex flex-column justify-content-start">
                                                                    <p className="fw-bold fs-8 mb-0">{item.snippet.channelTitle}</p>
                                                                    <span className="fs-10 mb-0">{`https://www.youtube.com/${tab == "Play List" ? "playlist?list=" : "channel/"}${tab == "Play List" ? item.id.playlistId : item.id.channelId}`}</span>
                                                                </div>
                                                            </div>
                                                        </Dropdown.Item>
                                                    ))}
                                                </div>
                                            </PerfectScrollbar>
                                        </Dropdown.Menu>
                                        : null}
                                </Dropdown>
                            }
                            {(tab == "Channel Url" && error) ||(tab == "Shorts" && shortError)||(tab == "Play List" && playListError)||(tab == "Keywords" && keywordError)||(tab == "Location" && locationError) ? <span className="errorMessage required">This field is required</span> : null}
                        </div>
                    </div>
                    {tab == "Location" ?
                        <div className="col-md-6">
                            <div className="form-group">
                                <div className="input-group">
                                    <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex" style={{ zIndex: 9 }}>
                                        {this.feedIcon(tab)}
                                    </span>
                                    <InputField name={"keyword"} className={`form-control form-control-lg fw-bolder px-9 ${(tab == "Location" && locationKeywordError)?'is-invalid':''}`} placeholder="Enter keywords" value={keyword} onChangeHandler={(e) => onKeywordChange(e.target.value)} />
                                </div>
                                {(tab == "Location" && locationKeywordError) ? <span className="errorMessage required">This field is required</span> : null}
                            </div>
                        </div>
                        : null
                    }

                </div>
                {/* <div className="hideforBSearch">
                    <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={checkBox} onChangeHandler={onCheckBoxChange} name={"moderation"}>
                            <span className="form-check-label">Manually approve posts before making them public?</span>
                        </CheckBoxField>
                    </label>
                </div> */}
                </>
        )
    }
}
