import React, { Component } from 'react';
import AccountPicker from '../../Elements/AccountPicker';
import { connect } from 'react-redux';
import CheckBoxField from '../../Elements/CheckboxField';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { selectAccount } from '../../../../../Services/Actions/addFeedAction';
import Select from 'react-select';
import InputField from '../../Elements/InputField';
import InfoSection from '../../Elements/InfoSection';

class InstagramTab extends Component {
    state = {
        allAccounts: [{ ConnectedAccounts: { id: 0, value2: 'loading...' } }],
        selectValue: {},
        formatedList: [],
        personalAccountList: [],
        selectPersonal: {}
    }

    componentDidMount() {
        this.getAccounts();
        this.getPersonalAccounts();
    }

    getPersonalAccounts = () => {
        const { personalAccount, onPersonalAccChange } = this.props;
        if (personalAccount && personalAccount.length > 0) {
            const formatedOption = this.formatOption(personalAccount, true);
            onPersonalAccChange(formatedOption[0]);
            this.setState({ personalAccountList: formatedOption, selectPersonal: formatedOption[0] });
        }
    }
    getAccounts = () => {
        const { connectedAcc, onSelectedChildAccountChange } = this.props
        var allAccounts = [];
        if (connectedAcc && connectedAcc.length > 0) {
            connectedAcc && connectedAcc.length && connectedAcc.map(acc => {
                if (acc.ConnectedAccounts.child && acc.ConnectedAccounts.child.length > 0) {
                    allAccounts = [...allAccounts, ...acc.ConnectedAccounts.child]
                }
                else {
                    this.setState({ allAccounts: [] })
                }
            })

            if (allAccounts && allAccounts.length > 0) {
                const formatedOption = this.formatOption(allAccounts, false);
                onSelectedChildAccountChange(formatedOption[0]);
                this.setState({ allAccounts, formatedList: formatedOption, selectValue: formatedOption[0] });
            }
        } else {
            onSelectedChildAccountChange(null)
            this.setState({ allAccounts: [] })
        }
    }



    onAccountChange = (obj) =>{
        const { value } = obj;
        const { onPersonalAccChange, onSelectedChildAccountChange, tab } = this.props;
        if (value && value != "") {
            if (tab == 'personal') {
                this.props.selectAccount(value);
                onPersonalAccChange(obj);
            } else {
                this.props.selectAccount(value)
                onSelectedChildAccountChange(obj)
            }
        }
        else {
            this.props.selectAccount(null)
        }
    }

    formatOption = (lists, isPersonal) => {
        const formatedList = lists.map((item) => ({ label: isPersonal ? item.ConnectedAccounts.accountName : item.ConnectedAccounts.value2, value: item.ConnectedAccounts.id }));
        if (formatedList && formatedList.length > 0) {
            return [...formatedList, { label: 'other', value: 0 }];
        }
        return []
    }

    getInfo = (tab) => {
        switch (tab) {
            case 'hashtag':
                return 'Instagram only allows posts from the last 24 hours. If there are no recent posts in the 24-hour duration, then the top 50 posts will be aggregated. Also, all your upcoming posts will be aggregated. Posts collected via hashtags will not include any information of the user.';

            case 'handle':
                return 'Initially 32 posts will be aggregated through handle after which all the upcoming posts will be aggregated.';

            case 'stories':
                return 'Initially 32 posts will be aggregated through handle after which all the upcoming posts will be aggregated.';

            case 'stories':
                return 'Initially stories from the last 24 hours will be shown after which all the upcoming stories will be aggregated.';

            case 'mentions':
                return 'You will only get upcoming posts after creating mentions feed.';

            case 'tagged':
                return 'Initially the latest 32 tagged posts on Instagram will be aggregated after which all the upcoming tagged posts will be aggregated.';

            case 'videos':
                return 'Initially 25 videos will be aggregated through handle after which all the upcoming videos will be aggregated.';

            case 'personal':
                return 'Initially 32 posts uploaded on personal accounts will be aggregated after which all the upcoming posts will be aggregated.';

            default:
                break;
        }
    }


    render() {
        const { tab, connectedAcc, hashtag, handle, onTextChange, personalAccount, handleError, onAddAccount, album, moderation, onCheckHandler, hashtagPost, excludeVideo, onlyReels, networkId, onAlbumChange, connetedAccount, onConnectedAccountChange, error } = this.props
        const { formatedList, allAccounts, selectValue, personalAccountList, selectPersonal } = this.state

        return (
            <>
                {tab == 'hashtag' || tab == "handle" ?
                    <div className="row">
                        <div className="col-md-12 mb-2">
                            <div className="form-group">
                                <InputField name={tab} className={`form-control form-control-lg fw-bolder px-9 ${(tab == "hashtag" && error) || (tab == "handle" && handleError)?'is-invalid':''}`} placeholder={`Enter user ${tab}`} value={tab == "hashtag" ? hashtag : handle} onChangeHandler={onTextChange} />
                            </div>
                            {(tab == "hashtag" && error) || (tab == "handle" && handleError) ? <span className="errorMessage required">This field is required</span> : null}
                        </div>
                    </div>
                    :
                    tab == "personal" ?
                        <div className="form-group text-center mb-6">
                            {personalAccount && personalAccount.length > 0 && personalAccountList && personalAccountList.length > 0 ?
                                <div className="col-md-12">
                                    <div className="form-group position-relative">
                                        <Select className="selectpicker" options={personalAccountList} classNamePrefix="selectpicker" defaultValue={selectPersonal} onChange={(e) => this.onAccountChange(e)} />
                                    </div>
                                </div>
                                :
                                <div className="py-5">
                                    <div className="btn btn-outline-gray btn-lg w-auto btn-clean d-inline-flex align-items-center btn-lg px-6" onClick={onAddAccount}>
                                        <span className="symbol symbol-25 me-3">
                                            <img src={baseUrl("media/icons/social-icons/instagram.svg")} />
                                        </span>
                                        <span className="text-dark-50 font-weight-bolder font-size-base">Connect Instagram</span>
                                    </div>
                                </div>
                            }
                        </div> :
                        <div className="form-group text-center mb-6">
                            {connectedAcc && connectedAcc.length > 0 && allAccounts && allAccounts.length > 0 ?
                                <div className="col-md-12">
                                    <div className="form-group position-relative">
                                        <Select className="selectpicker" options={formatedList} classNamePrefix="selectpicker" defaultValue={selectValue} onChange={(e) => this.onAccountChange(e)} />
                                    </div>
                                </div>
                                :
                                <div className="py-5">
                                    <div className="btn btn-outline-gray btn-lg w-auto btn-clean d-inline-flex align-items-center btn-lg px-6" onClick={onAddAccount}>
                                        <span className="symbol symbol-25 me-3">
                                            <img src={baseUrl("media/icons/social-icons/instagram.svg")} />
                                        </span>
                                        <span className="text-dark-50 font-weight-bolder font-size-base">Connect Instagram</span>
                                    </div>
                                </div>
                            }
                        </div>
                }
                {
                    tab == 'hashtag' ? <div className="hideforBSearch">
                        <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                            <CheckBoxField isChecked={hashtagPost} onChangeHandler={onCheckHandler} name={"hashtagPost"}>
                                <span className="form-check-label">Get posts containing hashtag only in caption.</span>
                            </CheckBoxField>
                        </label>
                    </div> : null
                }
                {
                    tab == "hashtag" || tab == 'handle' ?
                        <div className="hideforBSearch">
                            <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                <CheckBoxField isChecked={excludeVideo} onChangeHandler={onCheckHandler} name={"excludeVideo"}>
                                    <span className="form-check-label">Exclude Videos.</span>
                                </CheckBoxField>
                            </label>
                        </div> : null
                }
                {
                    tab != "stories" && tab != "tagged" ? <div className="hideforBSearch">
                        <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                            <CheckBoxField isChecked={onlyReels} onChangeHandler={onCheckHandler} name={"onlyReels"}>
                                <span className="form-check-label">Only Reels. <label class="new-tag new-tag-new ms-1 mb-0">New</label></span>
                            </CheckBoxField>
                        </label>
                    </div> : null
                }
                {/* <div className="hideforBSearch">
                    <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={moderation} onChangeHandler={onCheckHandler} name={"moderation"}>
                            <span className="form-check-label">Manually approve posts before making them public?</span>
                        </CheckBoxField>
                    </label>
                </div> */}
                {(connectedAcc && connectedAcc.length > 0) && ["hashtag", "handle"].includes(tab) ? <AccountPicker connectedAcc={connectedAcc} personalAccount={personalAccount} personal={tab == 'personal'} networkId={networkId} onConnectedAccountChange={onConnectedAccountChange} /> : null}
                <InfoSection text={this.getInfo(tab)} />
            </>
        )
    }
}

const mapDispatchToProps = dispatch => {
    return {
        selectAccount: (id) => dispatch(selectAccount(id)),
    }
}

export default connect(null, mapDispatchToProps)(InstagramTab)


