import React, { Component } from "react";
import { Dropdown } from "react-bootstrap";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { disconnectUser, selectAccount } from "../../../../Services/Actions/addFeedAction";
import ProfileImage from "../../ProfileImage/ProfileImage";

class AccountPicker extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedAccount: props.connectedAcc[0],
            accounts: []
        }
    }

    componentDidMount() {
        const { selectAccount, personalAccount, personal, connectedAcc } = this.props
        setTimeout(() => {
            if (connectedAcc && connectedAcc.length) {
                this.props.onConnectedAccountChange(this.props.connectedAcc[0].ConnectedAccounts.id);
                selectAccount(this.props.connectedAcc[0].ConnectedAccounts.id)
                this.setState({ accounts: personal ? personalAccount : connectedAcc, selectedAccount: personal ? personalAccount[0] : connectedAcc[0] })
            }
        }, 500);
    }


    disconnectAccount = (account) => {
        const {accounts} = this.state;
         
       this.props.disconnectAccount(account.ConnectedAccounts.id,()=>{
        if( accounts.length-1 >= 1){
            const updateAccountsList = accounts.filter(acc=>acc.ConnectedAccounts.id !== account.ConnectedAccounts.id);
            this.props.onConnectedAccountChange(updateAccountsList[0].ConnectedAccounts.id);
            this.props.selectAccount(updateAccountsList[0].ConnectedAccounts.id);
            this.setState({ accounts: updateAccountsList,selectedAccount:updateAccountsList[0] })
        }else{
            this.props.selectAccount(null);
            this.props.onConnectedAccountChange(null);
        }
       });
        
    }

    deleteAccount = (account) => {
        Swal.fire({
            title: '<strong>Confirm</strong>',
            icon: 'warning',
            text:
                'Do you want to disconnect account?',
            showCloseButton: true,
            showCancelButton: true,
            focusConfirm: true,
            confirmButtonText:
                'Yes',
            confirmButtonAriaLabel: 'Yes',
            cancelButtonAriaLabel: 'Thumbs down'
        }).then((result) => {
            if (result.value) {
                this.disconnectAccount(account)
            }
            else if (result.dismiss === "cancel") { }
        })
    }

    onAccountChange = (id) => {
        this.props.selectAccount(id ? id : null)
        this.props.onConnectedAccountChange(id);
        if(!id){
            this.setState({ selectedAccount: null });
        }
    }

    render() {

        const { selectedAccount, accounts } = this.state
        return (
            <>
                {(accounts && accounts.length>0 ) ? <Dropdown className="w-100 connected_acc__" id="connectedAll">
                    <Dropdown.Toggle variant="none" className="btn w-100 border d-flex justify-content-between align-items-center mt-5 ">
                        <span className="navi-link d-flex align-items-center justify-content-between cursor-pointer m-0 w-100" >
                            <div className="user_id d-flex align-items-center justify-content-start conndAcc_popuphover accountProfile">
                                <span className="symbol symbol-circle account-picker symbol-35 me-3">
                                    {
                                        selectedAccount ?

                                            <ProfileImage key={`${selectedAccount.ConnectedAccounts.accountName}_${selectedAccount.ConnectedAccounts.id}`} pic={selectedAccount.ConnectedAccounts.profilePicture} accountName={selectedAccount.ConnectedAccounts.accountName} network={selectedAccount.Network.name} />
                                            :
                                            <span className="symbol-label">
                                                <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70.06" viewBox="0 0 70 70.06">
                                                    <path id="Path_6644" data-name="Path 6644" d="M63.917,59.371a35.025,35.025,0,1,0-53.788-.006l-.045.049c.175.21.375.39.555.6.225.258.468.5.7.751.7.761,1.421,1.491,2.177,2.177.23.21.468.405.7.605.8.69,1.624,1.346,2.477,1.956.11.075.21.173.32.25v-.03a34.777,34.777,0,0,0,40.029,0v.03c.11-.078.208-.175.32-.25q1.276-.919,2.477-1.956c.233-.2.47-.4.7-.605.756-.688,1.476-1.416,2.177-2.177.233-.25.473-.493.7-.751.178-.208.38-.388.555-.6ZM37.025,17.016A11.258,11.258,0,1,1,25.767,28.274,11.258,11.258,0,0,1,37.025,17.016Zm-20,42.355A12.5,12.5,0,0,1,29.52,47.037H44.53A12.5,12.5,0,0,1,57.022,59.371,29.871,29.871,0,0,1,17.028,59.371Z" transform="translate(-2 -1.997)" fill="#c6c6c6"></path>
                                                </svg>
                                            </span>
                                    }
                                </span>
                                <div className="font-weight-bold conn_user_name text-dark">{selectedAccount ? selectedAccount.ConnectedAccounts.accountName : 'Add Another Account'}</div>
                            </div>
                            <input type="radio" id="" className="accountOption d-none" />
                        </span>
                    </Dropdown.Toggle>

                    <Dropdown.Menu className="w-100 navi flex-column navi-hover py-2 connectedAllAccounts">
                        <div className="navi flex-column navi-hover py-1 px-5 connectedAllAccounts">Select Account:</div>
                        {accounts && accounts.length ? accounts.map(acc => (
                            <Dropdown.Item className="navi-item" key={acc.ConnectedAccounts.accountId} onClick={() => { this.setState({ selectedAccount: acc }, () => this.onAccountChange(acc.ConnectedAccounts.id)) }}>
                                <span className="navi-link d-flex align-items-center justify-content-between cursor-pointer m-0 w-100" >
                                    <div className="user_id d-flex align-items-center justify-content-start conndAcc_popuphover accountProfile">
                                        <span className="symbol symbol-circle account-picker symbol-35 me-3">
                                            <ProfileImage key={`${acc.ConnectedAccounts.accountName}_${acc.ConnectedAccounts.id}`} pic={acc.ConnectedAccounts.profilePicture} accountName={acc.ConnectedAccounts.accountName} network={acc.Network.name} />
                                        </span>
                                        <div className="font-weight-bold conn_user_name text-dark">{acc.ConnectedAccounts.accountName}</div>
                                    </div>
                                    <span id="" className="btn-icon btn-circle btn-sm cursor-pointer" onClick={() => this.deleteAccount(acc)}>
                                        <p>disconnect</p>
                                    </span>
                                    <input type="radio" id="" className="accountOption d-none" />
                                </span>
                            </Dropdown.Item>
                        ))
                            : null
                        }
                        <Dropdown.Item className="navi-item" onClick={() => this.onAccountChange()}>
                            <span className="navi-link d-flex align-items-center justify-content-between flex-wrap cursor-pointer m-0 forAddNew accountProfile w-100" htmlFor="otherAccount">
                                <div className="user_id d-flex align-items-center justify-content-start conndAcc_popuphover accountProfile">
                                    <span className="symbol symbol-circle symbol-35 me-3">
                                        <span className="symbol-label">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70.06" viewBox="0 0 70 70.06">
                                                <path id="Path_6644" data-name="Path 6644" d="M63.917,59.371a35.025,35.025,0,1,0-53.788-.006l-.045.049c.175.21.375.39.555.6.225.258.468.5.7.751.7.761,1.421,1.491,2.177,2.177.23.21.468.405.7.605.8.69,1.624,1.346,2.477,1.956.11.075.21.173.32.25v-.03a34.777,34.777,0,0,0,40.029,0v.03c.11-.078.208-.175.32-.25q1.276-.919,2.477-1.956c.233-.2.47-.4.7-.605.756-.688,1.476-1.416,2.177-2.177.233-.25.473-.493.7-.751.178-.208.38-.388.555-.6ZM37.025,17.016A11.258,11.258,0,1,1,25.767,28.274,11.258,11.258,0,0,1,37.025,17.016Zm-20,42.355A12.5,12.5,0,0,1,29.52,47.037H44.53A12.5,12.5,0,0,1,57.022,59.371,29.871,29.871,0,0,1,17.028,59.371Z" transform="translate(-2 -1.997)" fill="#c6c6c6"></path>
                                            </svg>
                                        </span>
                                    </span>
                                    <div className="font-weight-bold conn_user_name text-dark">Add Another Account</div>
                                </div>
                                <span className="symbol symbol-circle symbol-40 me-1">
                                    <span className="svg-icon svg-icon-2x svg-icon-info">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70" viewBox="0 0 70 70">
                                            <path id="Path_6645" data-name="Path 6645" d="M59,24A35,35,0,1,0,94,59,35,35,0,0,0,59,24Zm0,64.615A29.615,29.615,0,1,1,88.615,59,29.615,29.615,0,0,1,59,88.615ZM75.154,59a2.692,2.692,0,0,1-2.692,2.692H61.692V72.462a2.692,2.692,0,0,1-5.385,0V61.692H45.538a2.692,2.692,0,1,1,0-5.385H56.308V45.538a2.692,2.692,0,0,1,5.385,0V56.308H72.462A2.692,2.692,0,0,1,75.154,59Z" transform="translate(-24 -24)" fill="#c6c6c6"></path>
                                        </svg>
                                    </span>
                                </span>
                                <input type="radio" id="otherAccount" className="accountOption d-none" />
                            </span>
                        </Dropdown.Item>
                    </Dropdown.Menu>
                </Dropdown>
                    :
                    null
                }

            </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        accountId: state.addFeed.selectedAccount
    }
}

const mapDispatchToProps = dispatch => {
    return {
        selectAccount: (id) => dispatch(selectAccount(id)),
        disconnectAccount:(accountId,callback)=>dispatch(disconnectUser(accountId,callback))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AccountPicker)