import React, { Component } from 'react';
import { Nav, Tab } from "react-bootstrap";
import { capitalizeFirstLetter } from '../../../../Services/Utils/utils';
import InputField from '../Elements/InputField';
import CheckBoxField from '../Elements/CheckboxField';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import {  createFeedAction } from '../../../../Services/Actions/addFeedAction';



class Giphy extends Component {
    constructor(props) {
        super(props);
        this.state = {
            search: "",
            sticker: "",
            handle: "",
            moderation: false,
            error: false,
            stickerError: false,
            handleError: false,
            loading: false,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype
        }
    }

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }


    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, stickerError: false, handleError: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }

    onManuallyApproveChange = (value) => this.setState({ moderation: value })

    onSubmit = () => {
        const { search, sticker, handle, moderation, selectedFilterTab, selectedFilterId } = this.state;
        const { collection, networkId,history } = this.props;

        if (selectedFilterTab == 'search' && search == '') {
            this.setState({ error: true });
        } else if (selectedFilterTab == 'search sticker' && sticker == '') {
            this.setState({ stickerError: true });
        } else if (selectedFilterTab == 'handle' && handle == '') {
            this.setState({ handleError: true });
        }
        else {

            var obj = {
                collection: collection != null ? collection.value : "",
                networkId: networkId,
                filter: selectedFilterId,
                moderation: moderation ? 1 : 0
            }
            if (selectedFilterId == 64) {
                obj['field'] = search
            } else if (selectedFilterId == 63) {
                obj['field'] = sticker
            } else if (selectedFilterId == 1) {
                obj['field'] = handle
            }

            this.setState({ error: false, handleError: false, stickerError: false, loading: true });

            createFeedAction(obj,networkId,() => {
                this.setState({ loading: false });
                this.props.closeAddFeed();
                history('/assets');
            });
        }
    }


    render() {
        const { filterTabs } = this.props;
        const { search, handle, sticker, moderation, handleError, error, loading, selectedFilterTab, stickerError } = this.state
        return (
            <>
                <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                    <div className="w-100">
                        <h6 className="fs-7 mb-3">Select the type of posts</h6>
                        <div className="card card-custom gutter-b">
                            <div className="card-body px-xxl-5">
                                <div className="">

                                    <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                        {
                                            filterTabs && filterTabs.map((filterTab) => {
                                                const { feedName, feedtype, filter } = filterTab;
                                                return <Nav.Item className="p-1" onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                    <Nav.Link eventKey={feedtype}>
                                                        <span className="svg-icon">
                                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                                <path fill="none" d="M0 0h24v24H0z" />
                                                                <path d="M12 9a3 3 0 1 0 0 6 3 3 0 0 0 0-6zm0-2a5 5 0 1 1 0 10 5 5 0 0 1 0-10zm6.5-.25a1.25 1.25 0 0 1-2.5 0 1.25 1.25 0 0 1 2.5 0zM12 4c-2.474 0-2.878.007-4.029.058-.784.037-1.31.142-1.798.332-.434.168-.747.369-1.08.703a2.89 2.89 0 0 0-.704 1.08c-.19.49-.295 1.015-.331 1.798C4.006 9.075 4 9.461 4 12c0 2.474.007 2.878.058 4.029.037.783.142 1.31.331 1.797.17.435.37.748.702 1.08.337.336.65.537 1.08.703.494.191 1.02.297 1.8.333C9.075 19.994 9.461 20 12 20c2.474 0 2.878-.007 4.029-.058.782-.037 1.309-.142 1.797-.331.433-.169.748-.37 1.08-.702.337-.337.538-.65.704-1.08.19-.493.296-1.02.332-1.8.052-1.104.058-1.49.058-4.029 0-2.474-.007-2.878-.058-4.029-.037-.782-.142-1.31-.332-1.798a2.911 2.911 0 0 0-.703-1.08 2.884 2.884 0 0 0-1.08-.704c-.49-.19-1.016-.295-1.798-.331C14.925 4.006 14.539 4 12 4zm0-2c2.717 0 3.056.01 4.122.06 1.065.05 1.79.217 2.428.465.66.254 1.216.598 1.772 1.153a4.908 4.908 0 0 1 1.153 1.772c.247.637.415 1.363.465 2.428.047 1.066.06 1.405.06 4.122 0 2.717-.01 3.056-.06 4.122-.05 1.065-.218 1.79-.465 2.428a4.883 4.883 0 0 1-1.153 1.772 4.915 4.915 0 0 1-1.772 1.153c-.637.247-1.363.415-2.428.465-1.066.047-1.405.06-4.122.06-2.717 0-3.056-.01-4.122-.06-1.065-.05-1.79-.218-2.428-.465a4.89 4.89 0 0 1-1.772-1.153 4.904 4.904 0 0 1-1.153-1.772c-.248-.637-.415-1.363-.465-2.428C2.013 15.056 2 14.717 2 12c0-2.717.01-3.056.06-4.122.05-1.066.217-1.79.465-2.428a4.88 4.88 0 0 1 1.153-1.772A4.897 4.897 0 0 1 5.45 2.525c.638-.248 1.362-.415 2.428-.465C8.944 2.013 9.283 2 12 2z" fill="currentColor" />
                                                            </svg>
                                                        </span>
                                                        {capitalizeFirstLetter(feedtype)}
                                                    </Nav.Link>
                                                </Nav.Item>
                                            })
                                        }
                                    </Nav>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="w-100">
                        <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                        <div className="card card-custom gutter-b">
                            <div className="card-body px-xxl-5">
                                <div className="">
                                    <Tab.Content>
                                        <Tab.Pane eventKey={"search"}>
                                            <div className="mb-2 mb-xl-3">
                                                <div className="">
                                                    <InputField name={"search"} className={`form-control form-control-lg fw-bolder px-9 ${error? 'is-invalid':''}`} placeholder="Search giphy keyword to fetch feed" value={search} onChangeHandler={this.onChangeHandler} />
                                                    {error ? <span className="invalid-feedback">This field is required</span> : null}
                                                </div>
                                            </div>
                                            {/* <div className="pb-2">
                                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                                    <CheckBoxField isChecked={moderation} onChangeHandler={this.onCheckHandler} name={"moderation"}>
                                                        <span className="form-check-label">Manually approve posts before making them public?</span>
                                                    </CheckBoxField>
                                                </label>
                                            </div> */}
                                        </Tab.Pane>
                                        <Tab.Pane eventKey={"search sticker"}>
                                            <div className="mb-2 mb-xl-3">
                                                <div className="">
                                                    <InputField name={"sticker"} className={`form-control form-control-lg fw-bolder px-9 ${stickerError? 'is-invalid':''}`} placeholder="Search giphy keyword to fetch feed" value={sticker} onChangeHandler={this.onChangeHandler} />
                                                    {stickerError ? <span className="invalid-feedback">This field is required</span> : null}
                                                </div>
                                            </div>
                                            {/* <div className="pb-2">
                                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                                    <CheckBoxField isChecked={moderation} onChangeHandler={this.onCheckHandler} name={"moderation"}>
                                                        <span className="form-check-label">Manually approve posts before making them public?</span>
                                                    </CheckBoxField>
                                                </label>
                                            </div> */}
                                        </Tab.Pane>
                                        <Tab.Pane eventKey={"handle"}>
                                            <div className="mb-2 mb-xl-3">
                                                <div className="">
                                                    <InputField name={"handle"} className={`form-control form-control-lg fw-bolder px-9 ${handleError? 'is-invalid':''}`} placeholder="Enter user handle" value={handle} onChangeHandler={this.onChangeHandler} />
                                                    {handleError ? <span className="invalid-feedback">This field is required</span> : null}
                                                </div>
                                            </div>
                                            {/* <div className="pb-2">
                                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                                    <CheckBoxField isChecked={moderation} onChangeHandler={this.onCheckHandler} name={"moderation"}>
                                                        <span className="form-check-label">Manually approve posts before making them public?</span>
                                                    </CheckBoxField>
                                                </label>
                                            </div> */}
                                        </Tab.Pane>
                                    </Tab.Content>
                                </div>
                            </div>
                        </div>
                    </div>
                </Tab.Container>
                <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
            </>
        )
    }
}

export default Giphy